import loadable from '@loadable/component';

// import Education from './Education';
// import Experience from './Experience';
// import Language from './Language';

const Education = loadable(() => import('./Education'));
const Experience = loadable(() => import('./Experience'));
const Language = loadable(() => import('./Language'));

const Resume = () => {
    return(
        <div id="resume" className="my-5 py-5">
            <div className="container my-5">
                <div className="row">
                    <div className="col">
                        <div className="text-title">Summary</div>
                        <p>An informatics engineering graduate with more than 5 years of work experience in IT. Interested in programming and in data processing. Due to my interest in programming, I have taken several courses but mostly self-taught since 2015. In my free time, I create videos for my youtube channel and sometimes play rubik's cube. I am also an ambitious person who love to learn new things and eager to make a good contribution to the company</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <div className="text-title">Education</div>
                        <Education />
                        <div className="text-title">Language</div>
                        <Language />
                    </div>
                    <div className="col-md-6">
                        <div className="text-title">Experience</div>
                        <Experience />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Resume;